<template>
    <div id="app">
			<div class="top">
				<img src="./../assets/images/pub_back1.png" alt="" @click="$router.go(-1)">
			</div>
			<div class="login">
				<span>密码登录</span> | 
				<span>验证码登录</span>
			</div>
			<div class="t">
				<p>用户名</p>
				<div class="i">
					<input type="text" v-model="uname">
					<img src="./../assets/images/del_type.png" alt="">
				</div>
			</div>
			<div class="t">
				<p>密码</p>
				<div class="i">
					<input type="text" v-model="upwd">
					<img src="./../assets/images/pwd_open.png" alt="">
				</div>
			</div>
			<div class="dl">
				<div class="left">
					<span>忘记密码</span> | 
					<span @click="$router.push('/ToRegister')">注册账号</span>
				</div>
				<button @click="login">登录</button>
			</div>
    </div>
</template>
<script>
import Cookie from '../assets/js/Cookie';
export default {
    data(){
        return {
			uname:"",
			upwd:"",
			}
    },
    methods:{
        login(){
			this.$http({
			    method:"POST",
			    url:"http://www.520mg.com/member/index_login.php",
			    data:`fmdo=login&dopost=login&userid=${this.uname}&pwd=${this.upwd}`,
			    configs:{withCredentials:true}
			})
			.then(res=>{
			    console.log(res.data);
			    if(res.data.status==1){
					this.$notify({type:'success',message:res.data.msg});
					Cookie.setCookie("uname",this.uname);
					let redirect = this.$route.query.redirect;
					console.log("redirect",redirect);
					if(redirect){
						this.$router.replace(redirect);
					}else{
					this.$router.replace("/my");
					}
				}else{
                    this.$notify({type:'danger',
                    message:res.data.msg});
                }
            })
            
        }
    }
}
</script>
<style scoped>
#app{
    padding: 0 0.4rem;
    background:url(./../assets/images/bg1.png)no-repeat 0 0/100% 100%;
}
.top{
    width: 100%;
    line-height: 0.88rem;
}
.top img{
    width: 0.2rem;
    height: 0.3rem;
}
.login{
    width: 100%;
    height: 1.68rem;
    font-size: 0.36rem;
    color: #fff;
    text-align: center;
}
.t{
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    height: 1rem;
    font-size: 0.24rem;
    color: #fff;
    margin-bottom: 0.5rem;
}
.t .i{
    width: 100%;
    padding-bottom: 0.1rem;
    border-bottom: 1px solid #eee;
}
.t .i input{
    width: 85%;
    border: none;
    background-color: rgba(0,0,0,0);
}
.t img{
    height: 0.3rem;
}
.dl{
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.dl .left{
    font-size: 0.22rem;
    color: #eee;
}
.dl button{
    width: 1.6rem;
    height: 0.8rem;
    font-size: 0.26rem;
    color: #555;
    font-weight: bold;
    border: none;
    border-radius: 0.1rem;
    background-color: #fff;
}
</style>