<template>
    <div class="nav-wrap">
        <div class="nav" ref="scroll">
            <div :class="{'active':index==current}" @click="selected($event,index)" v-for="(item,index) in navList" :key="index" class="nav-item">
                {{item.name}}
            </div>
        </div>
        <div class="nav-toggle" :class="{'up':showLay==true}" @click="showLay=!showLay">
            <img src="./../assets/images/bottom.png" alt="">
        </div>
        <div class="nav-lay" v-if="showLay">
            <div class="nav-title">全部</div>
            <div class="nav-buttons">
                <div class="btn" @click="btnclick(index)"
                :class="{'active':index==current}" v-for="(item,index) in navList" :key="index">
                    {{item.name}}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props:["navList"],
    methods:{
        btnclick(index){
            this.showLay=false;
            this.current=index;
            // 设置当前选中的current
            let items = document.querySelectorAll(".nav-item");
            this.selected({target:items[index]},index);
        },
        selected(e,index){
            this.current = index;
            let ew = e.target.offsetWidth;
            let el = e.target.offsetLeft; // 元素左侧偏移值
            let elem = this.$refs.scroll;
            let vw = elem.offsetWidth;
            console.log(ew,el,vw);
            elem.scrollLeft = el-vw/2+ew/2;
        }
    },
    data(){
        return{
            current:0,
            showLay:false
        }
    }
}
</script>
